й */\n        pointer-events: none;\n\n        /* Отключение скроллбара */\n        &::-webkit-scrollbar {\n            display: none;\n        }\n\n        a {\n            transition: transform 0.4s, opacity 0.4s;\n            z-index: 2;\n\n            &:hover {\n                box-shadow: 0 0 18.4px 0 #FFFFFF4A;\n                transition: box-shadow 0.3s ease;\n            }\n        }\n\n        @media (max-width: 1023px) {\n            margin-top: 14px;\n        }\n    }\n\n    &.is_open {\n        max-height: 36px;\n    }\n\n    @media (max-width: 1023px) {\n        &.is_open {\n            max-height: 36px;\n        }\n\n        h2 {\n            font-size: 18px;\n        }\n    }\n}\n\n.slide-right-enter-active,\n.slide-right-leave-active {\n    transition: transform 0.3s ease, opacity 0.3s ease;\n}\n\n.slide-right-enter {\n    transform: translateX(-100%);\n}\n\n.slide-right-leave-to {\n    transform: translateX(100%);\n}\n\n.live-drop {\n    display: flex;\n    align-items: flex-start;\n    background-color: rgb(255, 255, 255, 0.05);\n    padding: 8px;\n    gap: 10px;\n    font-size: 18px;\n    border: 1px solid rgb(255, 255, 255, 0.15);\n    border-radius: 24px;\n    font-weight: 500;\n    font-family: Oswald !important;\n    text-decoration: none;\n    flex-shrink: 0;\n    user-select: none;\n\n    &__img {\n        user-select: none;\n        width: 82px;\n        height: 100%;\n        aspect-ratio: 122/182;\n        object-fit: cover;\n        border-radius: 16px;\n\n        @media (max-width: 1023px) {\n            width: 60px;\n        }\n    }\n\n    &__info {\n        display: flex;\n        flex-direction: column;\n        justify-content: space-between;\n        height: 100%;\n        width: 158px;\n        padding-top: 8px;\n        padding-bottom: 8px;\n\n        @media (max-width: 1023px) {\n            padding: 0;\n        }\n    }\n\n    &__number {\n        margin-bottom: 6px;\n        font-family: 'Inter', sans-serif;\n        font-size: 24px;\n        font-weight: 700;\n        line-height: 29.05px;\n        text-align: left;\n        text-underline-position: from-font;\n        text-decoration-skip-ink: none;\n        color: #fff;\n\n        @media (max-width: 1023px) {\n            font-size: 18px;\n            line-height: 21.78px;\n        }\n    }\n\n    &__username {\n        margin-bottom: 16px;\n        font-family: 'Inter', sans-serif;\n        font-size: 16px;\n        font-weight: 500;\n        line-height: 19.36px;\n        text-align: left;\n        text-underline-position: from-font;\n        text-decoration-skip-ink: none;\n        color: #9E9AA1;\n\n        @media (max-width: 1023px) {\n            font-size: 14px;\n            line-height: 16.94px;\n        }\n    }\n\n    &__slot {\n        font-family: 'Inter', sans-serif;\n        font-size: 18px;\n        font-style: italic;\n        font-weight: 400;\n        line-height: 21.78px;\n        text-align: left;\n        text-underline-position: from-font;\n        text-decoration-skip-ink: none;\n        color: #fff;\n\n        @media (max-width: 1023px) {\n            font-size: 16px;\n            font-weight: 400;\n            line-height: 19.36px;\n        }\n    }\n}\n"],"sourceRoot":""}]);
